/*START THEM RADIO BUTTON*/
.radioContainer {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Hide the browser's default radio button */
.radioContainer input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

/* Create a custom radio button */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: #eee;
  border: 0.15px #eee solid;
  border-radius: 50%;
}
/* On mouse-over, add a grey background color */
.radioContainer:hover input ~ .checkmark {
  background-color: #ccc;
}

input[value="option-1"] + .checkmark {
  background-color: #000;
}

input[value="option-2"] + .checkmark {
  background-color: #dc4c16;
}

input[value="option-3"] + .checkmark {
  background-color: #ffd633;
}

input[value="option-4"] + .checkmark {
  background-color: #800000;
}

#designOptions {
  position: fixed;
  z-index: 5;
  margin-top: 13%;
  color: #fff;
}
#designOptions ul,
#designOptions ul li {
  list-style-type: disc;
  margin-left: -18px !important;
}

/* When the radio button is checked, add a blue background*/
/*.radioContainer input:checked ~ .checkmark {
  background-color: #2196f3;
}*/

/* Create the indicator (the dot/circle - hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the indicator (dot/circle) when checked */
.radioContainer input:checked ~ .checkmark:after {
  display: block;
}

/* Style the indicator (dot/circle) */
.radioContainer .checkmark:after {
  top: 9px;
  left: 8px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: white;
}

.radioContainer input[value="option-2"] .checkmark:after {
  top: 9px;
  left: 9px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #dc4c16;
}

/*END THEM RADIO BUTTON*/

